import { Dispatch } from "redux";
import { ActionTypes } from "./types";
/**
 * * ****************************** INTERFACES *****************************
 */

export interface System {
  navigation: boolean;
  page_has_sub_nav: boolean;
}

//* ********************** ACTION TYPE INTERCACES ********************** */

export interface NavToogleInterface {
  type: ActionTypes.NAVIGATION_TOOGLE;
}

export interface PageHasSubNavInterface {
  type: ActionTypes.PAGE_HAS_SUB_NAV;
  payload: boolean;
}

/**
 * * ****************************** ACTIONS *****************************
 */

/**
 * @description Activate/desactivate navigation
 * @returns null
 */
export const NavToogle = () => {
  return (dispatch: Dispatch) => {
    dispatch<NavToogleInterface>({
      type: ActionTypes.NAVIGATION_TOOGLE,
    });
  };
};

/**
 * @description Activate/desactivate navigation
 * @returns null
 */
export const PageHasSubNav = (status: boolean) => {
  return (dispatch: Dispatch) => {
    dispatch<PageHasSubNavInterface>({
      type: ActionTypes.PAGE_HAS_SUB_NAV,
      payload: status,
    });
  };
};
